.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 24px;
  position: relative;
  &-left {
    display: flex;
    align-items: center;
  }
  &-menu {
    box-shadow: 0px 2px 8px 0px #00000026;
    border-radius: 0px 0px 35px;
    padding: 8px 12px;
    border: 1px solid #EAEAEA;
    width: 72px;
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 30;
    background: #FFFFFF;
    &-open {
      display: none;
    }
    &.active {
      .header-menu-close {
        display: none;
      }
      .header-menu-open {
        display: block;
      }
    }
    &:hover {
      background: #ECB100;
      cursor: pointer;
      svg {
        fill: #FFFFFF!important;
      }
    }
  }
  &-logo {
    max-width: 120px;
    width: 100%;
    margin-left: 20px;
    img {
      width: 100%;
      height: 38px;
      object-fit: contain;
    }
  }

  &-center {
    align-items: center;
    display: none;
    @media (min-width: 768px) {
      display: flex;
    }
    &-item {
      padding: 12px 24px 12px 16px;
      display: flex;
      align-items: center;
      background: #FCFCFC;
      border: 1px solid #E7E7E7;
      margin-left: 12px;
      span {
        margin-left: 8px;
        font-weight: 500;
        font-size: 14px;
      }
      &:hover {
        background: #ECB100;
        color: #FFFFFF;
        cursor: pointer;
      }
    }
  }

  &-right {
    display: flex;
    align-items: center;
    &-search {
      padding: 12px 20px 12px 16px;
      min-width: 48px;
      border-radius: 999px;
      background: #FCFCFC;
      border: 1px solid #E7E7E7;
      display: flex;
      align-items: center;
      span {
        margin-left: 8px;
        font-weight: 500;
        font-size: 14px;
      }
      &:hover {
        background: #ECB100;
        color: #FFFFFF;
        cursor: pointer;
      }
    }
    &-account {
      padding: 6px 16px;
      border-radius: 999px;
      background: #FCFCFC;
      border: 1px solid #E7E7E7;
      margin-left: 12px;
      &:hover {
        background: #ECB100;
        cursor: pointer;
      }
    }
  }

  .gamburger {
    position: fixed;
    left: 0;
    right: 0;
    top: 75px;
    bottom: 0;
    width: 100%;
    background: #fcfcfc;
    z-index: 25;
    overflow: hidden;
    height: 0;
    transition: all 0.3s;
    @media (min-width: 768px) {
      top: 0;
    }
    &-container {
      display: flex;
      height: 100%;
      position: relative;
      flex-direction: column;
      @media (min-width: 768px) {
        flex-direction: row;
        justify-content: space-between;
      }
    }
    &-item {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-direction: column;
      padding-left: 24px;
      @media (min-width: 768px) {
        position: absolute;
        top: 30%;
        left: 50%;
        transform: translateX(-50%);
        align-items: center;
      }
    }
    &-block {
      position: relative;
      transition: all 0.3s;
      @media (min-width: 768px) {
        border-right: 1px solid #D4D8D9;
        width: 25%;
      }
      &:last-child {
        border-top: none;
      }
      &:hover {
        background: #ECB100;
        transition: all 0.3s;
        cursor: pointer;
        svg {
          fill: #FFFFFF!important;
          path {
            stroke: #FFFFFF!important;
          }
        }
        .gamburger__link {
          color: #000000;
          transition: all 0.3s;
        }
      }
    }
    &-name {
      display: flex;
      align-items: center;
      flex-direction: row;
      @media (min-width: 768px) {
        flex-direction: column;
      }
    }
    &__link {
      margin-top: 8px;
      color: #FFFFFF;
      transition: all 0.3s;
      align-self: flex-start;
      padding-left: 42px;
      &:hover {
        text-decoration: underline;
      }
      @media (min-width: 768px) {
        padding-left: 0px;
      }
    }
    &__title {
      font-size: 20px;
      color: #000000;
      margin-left: 12px;
      @media (min-width: 768px) {
        margin-bottom: 18px;
        margin-left: 0px;
      }
    }
    &-footer {
      position: absolute;
      bottom: 25px;
      display: flex;
      align-items: center;
      justify-content: center;
      left: 50%;
      transform: translateX(-50%);
      &-link {
        padding: 6px 16px;
        border-radius: 999px;
        background: #FCFCFC;
        border: 1px solid #E7E7E7;
        width: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 48px;
        margin-left: 12px;
        span {
          margin-left: 8px;
          font-weight: 500;
          font-size: 14px;
          color: #000000;
        }
        &:hover {
          background: #ECB100;
          color: #FFFFFF;
          cursor: pointer;
          svg {
            fill: #FFFFFF!important;
            stroke: #FFFFFF!important;
            path {
              fill: #FFFFFF!important;
            }
          }
        }
      }
    }
    &.active {
      height: 100%;
      transition: all 0.3s;
    }
  }
}
