*,
*::before,
*::after {
	box-sizing: border-box;
}

* {
	font-family: Inter,sans-serif;
}

a {
	text-decoration: none;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
	background: #FCFCFC;
}

body {
	font-size: 1rem;
}

.main {
	padding-right: 16px;
	padding-left: 16px;
	@media (min-width: 768px) {
		padding-right: 72px;
		padding-left: 72px;
	}
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}
.container {
	max-width: 1300px;
	width: 100%;
	margin-right: auto;
	margin-left: auto;
}
