.coming-soon {
  display: flex;
  align-items: center;
  padding: 8px;
  h4 {
    font-weight: 700;
    font-size: 28px;
    margin-left: 8px;
    color: #000000;
  }
  a {
    padding: 12px 16px 12px 20px;
    background: #FCFCFC;
    border: 1px solid #E7E7E7;
    border-radius: 999px;
    margin-left: 8px;
    span {
      font-weight: 500;
      font-size: 14px;
      color: #000000;
    }
    &:hover {
      background: #ECB100;
      color: #FFFFFF;
      cursor: pointer;
    }
  }
}

.booking {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 10px;
  grid-row-gap: 16px;
  margin-top: 24px;
  padding-bottom: 72px;
  &-item {
    .time {
      font-size: 18px;
      color: #D4D8D9;
    }
    .images {
      position: relative;
      border-radius: 12px;
      margin-top: 12px;
      img {
        width: 100%;
        object-fit: cover;
        border-radius: 12px;
      }
      .date {
        position: absolute;
        left: 15px;
        top: 15px;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: #FFFFFFE6;
        box-shadow: 0px 4px 16px 0px #00000033;
        border-radius: 12px;
        padding: 8px 12px;
        span {
          font-size: 14px;
          color: #0070f3;
        }
      }
      .cost {
        position: absolute;
        right: 15px;
        bottom: 15px;
        background: #FFFFFFE6;
        box-shadow: 0px 4px 16px 0px #00000033;
        border-radius: 12px;
        padding: 8px 12px;
        span {
          font-size: 14px;
          color: #0070f3;
        }
      }
    }
  }
}


.event {
  .container {
    max-width: 1080px;
  }
  &-item {
    width: 100%;
    border-radius: 12px;
    box-shadow: 0px 4px 16px 0px #00000033;
    margin-top: 42px;
    .images {
      height: 350px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 12px 12px 0 0;
        object-fit: cover;
      }
    }
    .poster {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      //margin-top: 48px;
      padding: 48px 24px;
      @media (min-width: 768px) {
        flex-direction: row;
      }
      &-left {
        max-width: 600px;
        width: 100%;
        padding-right: 32px;
        h3 {
          font-size: 28px;
          font-weight: 500;
          padding-bottom: 16px;
        }
        p {
          font-size: 16px;
          color: #535353;
          margin-top: 14px;
          &:first-child {
            margin-top: 0;
          }
        }
      }
      &-right {
        border-radius: 12px;
        padding: 24px;
        border: 1px solid #DDDDDD;
        max-width: 350px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-top: 42px;
        @media (min-width: 768px) {
          max-width: 240px;
        }
        @media (min-width: 992px) {
          max-width: 350px;
        }
        .date {
          display: flex;
          align-items: center;
          span {
            font-size: 14px;
            color: #000000;
            font-weight: 700;
          }
        }
        .location {
          display: flex;
          align-items: center;
          margin-top: 8px;
          svg {
            margin-right: 8px;
          }
          span {
            font-size: 14px;
            color: #000000;
            font-weight: 500;
          }
          .line {
            margin-right: 8px;
            margin-left: 8px;
            height: 20px;
            width: 1px;
            background: #0074FF;
          }
        }
        .cost {
          display: flex;
          align-items: center;
          margin-top: 24px;
          span {
            font-size: 24px;
            font-weight: 700;
            color: #535353;
          }
        }
        .link {
          display: flex;
          align-items: center;
          border-radius: 6px;
          padding: 12px 24px;
          background: #F5D90A;
          width: fit-content;
          margin-top: 24px;
          span {
            font-weight: 500;
            white-space: pre;
            font-size: 14px;
            color: #35290F;
            margin-right: 8px;
          }
        }
      }
    }
  }
}
