.footer {
  display: flex;
  flex-direction: column;
  &-social {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #D4D8D9;
    padding-top: 24px;
    padding-bottom: 24px;
    &__item {
      display: flex;
      align-items: center;
      &:first-child {
        max-width: 85px;
        width: 100%;
        img {
          width: 100%;
        }
      }
    }
    &__link {
      padding: 6px 16px;
      border-radius: 999px;
      background: #FCFCFC;
      border: 1px solid #E7E7E7;
      min-width: 48px;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 48px;
      margin-left: 12px;
      span {
        margin-left: 8px;
        font-weight: 500;
        font-size: 14px;
        color: #000000;
      }
      &:hover {
        background: #ECB100;
        color: #FFFFFF;
        cursor: pointer;
      }
    }
  }
  &-info {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid #D4D8D9;
    padding-top: 24px;
    padding-bottom: 24px;
    &-block {
      display: flex;
      flex-direction: column;
    }
    &__link {
      border-radius: 6px;
      padding: 12px 24px;
      font-weight: 500;
      font-size: 14px;
      color: #000000;
      margin-top: 8px;
      &:first-child {
        margin-top: 0;
      }
    }
  }
  &-copyright {
    display: flex;
    flex-direction: column;
    border-top: 1px solid #D4D8D9;
    &-block {
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 16px 0;
      a {
        font-weight: 500;
        font-size: 14px;
        color: #000000;
        padding: 6px 16px;
      }
      .hr {
        width: 1px;
        height: 48px;
        background: #D4D8D9;
      }
      span {
        font-size: 14px;
        color: #535353;
      }
    }
    &-link {
      padding: 6px 16px;
      border-radius: 999px;
      background: #FCFCFC;
      border: 1px solid #E7E7E7;
      width: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 48px;
      margin-left: 12px;
      span {
        margin-left: 8px;
        font-weight: 500;
        font-size: 14px;
        color: #000000;
      }
      &:hover {
        background: #ECB100;
        color: #FFFFFF;
        cursor: pointer;
        svg {
          fill: #FFFFFF!important;
          stroke: #FFFFFF!important;
          path {
            fill: #FFFFFF!important;
          }
        }
      }
    }
  }
}
